@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
* {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

*::before {
  box-sizing: border-box;
}

*::after {
  box-sizing: border-box;
}

body {
  height: 100vh;
  background-image:url('https://i02piccdn.sogoucdn.com/e77dcfd4ff9195e2');
}

ul, ol, li{
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit
}

h1, h2, h3, h4, h5, h6{
  font-size:100%;
  color:rgb(51, 51, 51);
}

address, cite, dfn, em, var {
  font-style:normal;
}

code, kbd, pre, samp {
  font-family: 'Poppins', sans-serif;
}

small{
  font-size:12px;
}

sup {
  vertical-align:text-top;
}

sub{
  vertical-align:text-bottom;
}

legend {
  color:#000;
}

fieldset, img {
  border:0;
}

button, input, select, textarea {
  font-size:100%;
}

table {
  border-collapse:collapse; border-spacing:0;
}

html,body,#root{
  width: 100%;
  height: 100%;
}



